import {FileMessage, UploadFiles} from "@/pages/index/indexClass"

export default function methods() {

	function select(e : { tempFiles : { path : any, name : string, extname : string }[]; }) {
		let num : number = e.tempFiles.length
		let fileList : FileMessage[] = []
		for (let i = 0; i < num; i++) {
			const filePath = e.tempFiles[i].path;
			const fileName = e.tempFiles[i].name
			const fileExtname = e.tempFiles[i].extname
			let fileMessage = new FileMessage(filePath, fileName, fileExtname)
			fileList.push(fileMessage)
		}
		console.log(e)
		return fileList
	}

	function progress(e) : void {
		console.log(e, "上传文件");
	}

	function success() : void {
		console.log('上传成功')
	}

	// 上传失败
	function fail(e) : void {
		console.log(e, '上传失败：')
	}

	function imagesUpload(uploadFilesList : UploadFiles[]) {
		let num = uploadFilesList.length
		for (let i = 0; i < num; i++){
			uni.uploadFile(uploadFilesList[i])
		}
	}

	return { select, progress, success, fail, imagesUpload }
}